import React from 'react';
import PrivateRoute from "./UI/components/PrivateRoute";
import {Switch, Route} from "react-router-dom";
import BlogPosts from './UI/Routes/Blogs';
import UserProfile from './UI/Routes/UserProfile';
import NewBlogCreation from './UI/Routes/NewBlogCreation';
import ExpandBlog from './UI/components/ExpandBlog/ExpandBlog.js';
import EditBlogComponent from './UI/components/EditBlog/editBlogComponent.js';
import SignUpComponent from './UI/components/SignUp/SignUpComponent';
import SignIn from './UI/components/SignIn/SignIn';
import '../node_modules/react-draft-wysiwyg/dist/react-draft-wysiwyg.css';

function App() {
  return (
    <div style = {{
      textAlign: "center"
    }}>
      
    <Switch>
      <PrivateRoute path = "/profile" exact>
        <UserProfile />
      </PrivateRoute>
      <Route path = {process.env.PUBLIC_URL + "/"} exact><BlogPosts /></Route>
      <PrivateRoute path = "/createNewBlog" exact><NewBlogCreation/></PrivateRoute>
      <PrivateRoute path = "/expandBlog" exact><ExpandBlog/></PrivateRoute>
      <PrivateRoute path = "/editBlog" exact><EditBlogComponent/></PrivateRoute>
      <Route path = "/signup" exact><SignUpComponent /></Route>
      <Route path = "/signin" exact><SignIn /></Route>
    </Switch>

    </div>
  );
}
export default App;