import React from 'react';
import { useSelector } from "react-redux";
import { useLocation, useHistory } from 'react-router-dom';
import { useFirebase } from "react-redux-firebase";
import '../../../sass/expandblog.scss';

const ExpandBlog = () => {
    const location = useLocation();
    const { displayName } = useSelector((state) => state.firebase.auth);
    const { title, image, smallDesc, desc, isSignedIn, email } = (location && location.state) || {};
    const firebase = useFirebase();
    const history = useHistory();

    const signInWithGoogle = () => {
        firebase
            .login({
                provider: "google",
                type: "popup"
            })
            .then(() => {
                history.push("/profile");
            });
    };

    const signout = () => {
        firebase
            .auth()
            .signOut()
            .then(() => {
                history.push(process.env.PUBLIC_URL + "/");
            });
    };

    return (
        <div>
            <nav className="navbar navbar-expand-sm bg-dark navbar-dark fixed-top navbarContainer">
                <a href="#" className="navbar-brand"><strong>B</strong>log</a>
                {isSignedIn ? 
                    <div className='dropdown profileDropdown'>
                        <button className='btn btn-info rounded-circle w-38 text-uppercase' style={{cursor: 'pointer'}} data-toggle="dropdown">{displayName !== null ? displayName[0] : email[0]}</button>
                        <div className="dropdown-menu dropdown-menu-right">
                            <a className="dropdown-item font-weight-bold" href="#">{displayName !== null ? displayName : email}</a>
                            <a className="dropdown-item" href="#" onClick={(event) => { event.preventDefault(); signout(); }}>SignOut</a>
                        </div>
                    </div>
                :
                    <button className='btn btn-outline-info signInBtn' onClick={(event) => { event.preventDefault(); signInWithGoogle(); }}>SignIn</button>
                }
            </nav>
            <section>
                <div className="container expandBlogContainer mb-5">
                    <h1><strong>{title}</strong></h1>
                    <p className="blogImg"><img src={image ? image : "https://images.unsplash.com/photo-1493934558415-9d19f0b2b4d2?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60"} alt="Card image" width="730px" height="486px" /></p>
                    <div className="blogContent">
                        <article>
                            <p className="smallDesc">{smallDesc}</p>
                            <p className="blogDescription">{desc}</p>
                        </article>
                    </div>
                </div>
            </section>
            <footer className="jumbotron text-center bg-dark mb-0 text-light">
                <h2>Follow Us On : </h2>
                <p className="mt-4">
                    <i className="fa fa-instagram fa-lg mr-4" aria-hidden="true"></i>
                    <i className="fa fa-twitter fa-lg mr-4" aria-hidden="true"></i>
                    <i className="fa fa-facebook-official fa-lg mr-4" aria-hidden="true"></i>
                    <i className="fa fa-youtube-play fa-lg" aria-hidden="true"></i>
                </p>
            </footer>
        </div>
    )
};
export default ExpandBlog;