import React from 'react';
import { useSelector } from "react-redux";
import { useFirestoreConnect, useFirebase, useFirestore } from "react-redux-firebase";
import { useLocation, useHistory } from "react-router-dom";
import "../../sass/userProfile.scss";

const UserProfile = () => {
    const location = useLocation();
    const { displayName, uid } = useSelector((state) => state.firebase.auth);
    const { email } = (location && location.state) || {};

    useFirestoreConnect({
        collection: `users/${uid}/blogs`,
        storeAs: "blogs",
    });
    const blogs = useSelector((state) => state.firestore.data.blogs);
    const firestore = useFirestore();
    const firebase = useFirebase();
    const history = useHistory();

    const signout = () => {
        firebase
            .auth()
            .signOut()
            .then(() => {
                history.push(process.env.PUBLIC_URL + "/");
            });
    };

    const handlBtn = (event, data) => {
        event.preventDefault();
        if (event.target.name === "readMoreBtn") {
            history.push({
                pathname: '/expandBlog',
                state: {
                    title: data.title,
                    image: data.image,
                    smallDesc: data.smallDescription,
                    desc: data.description,
                    isSignedIn: true
                }
            });
        }
    }

    const handleEditBtn = (event, data) => {
        event.preventDefault();
        if (event.target.name === "editBtn") {
            history.push({
                pathname: "/editBlog",
                state: {
                    title: data.title,
                    image: data.image,
                    smallDesc: data.smallDescription,
                    desc: data.description,
                    blogID: data.blogID,
                    isSignedIn: true
                }
            });
        }
    }

    const handleDelete = (event, blogID) => {
        if (event.target.name === "delete") {
            firestore.collection("users").doc(uid).collection("blogs").doc(blogID).delete().then(function () {
                alert("Item successfully deleted.");
            }).catch(function (error) {
                console.error("Error removing document: ", error);
            });
        }
    }

    const handleClick = () => {
        history.push("/createNewBlog");
    }

    return (
        <div>
            <nav className="navbar navbar-expand-sm bg-dark navbar-dark navbarContent">
                <a href="#" className="navbar-brand"><strong>B</strong>log</a>
                <h2 className='text-light d-inline p-0 ml-auto mt-auto mb-auto'>Create Your Blog</h2>
                <div className='dropdown profileDropdown'>
                    <button className='btn btn-info rounded-circle roundProfileBtn text-uppercase' data-toggle="dropdown">{displayName !== null ? displayName[0] : email[0]}</button>
                    <div className="dropdown-menu dropdown-menu-right">
                        <a className="dropdown-item font-weight-bold" href="#">{displayName !== null ? displayName : email}</a>
                        <a className="dropdown-item" href="#" onClick={(event) => { event.preventDefault(); signout(); }}>SignOut</a>
                    </div>
                </div>
            </nav>
            <section>
                <div className='container blogContainer mb-5'>
                    <div className='d-flex containerHeading'>
                        <h3>Your Dashboard</h3>
                        <button className='btn btn-dark' onClick={(event) => { event.preventDefault(); handleClick(); }}>CREATE BLOG</button>
                    </div>
                    <div className="container-fluid cardContainer">
                        <div className="row">
                            {blogs && Object.values(blogs).map((blog) => (
                                <div>
                                    { blog === null ?
                                        ''
                                        :
                                        <div className="col-lg-4 col-md-4 col-12">
                                            <center>
                                                <div className="card blogCard">
                                                    <img className="card-img-top cardImg" src={blog.image ? blog.image : 'https://images.unsplash.com/photo-1493934558415-9d19f0b2b4d2?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60'} alt='card' />
                                                    <div className="card-body text-justify">
                                                        <h4 className="card-title">{blog.title}</h4>
                                                        <p>{blog.smallDescription}</p>
                                                        <a href="#" className="btn btn-primary" onClick={(e) => handlBtn(e, blog)} name='readMoreBtn'>Read More</a>
                                                        <a href="#" className="btn btn-warning d-inline-block cardEditBtn" onClick={(e) => handleEditBtn(e, blog)} name='editBtn'>Edit</a>
                                                        <a href="#" className="btn btn-danger float-right" onClick={(e) => handleDelete(e, blog.blogID)} name='delete'>Delete</a>
                                                    </div>
                                                </div>
                                            </center>
                                        </div>
                                    }
                                </div>
                            ))
                            }
                        </div>
                    </div>
                </div>
            </section>
            <section className="mb-5">
                {!blogs ? <div className='border border-0 bg-light nothingCreatedContent'>
                    Nothing Created Yet
                        </div> : ''}
            </section>

            <footer className="jumbotron text-center bg-dark mb-0 text-light">
                <h2>Follow Us On : </h2>
                <p className="mt-4">
                    <i className="fa fa-instagram fa-lg mr-4" aria-hidden="true"></i>
                    <i className="fa fa-twitter fa-lg mr-4" aria-hidden="true"></i>
                    <i className="fa fa-facebook-official fa-lg mr-4" aria-hidden="true"></i>
                    <i className="fa fa-youtube-play fa-lg" aria-hidden="true"></i>
                </p>
            </footer>
            
        </div>
    )
}

export default UserProfile;